.App {
  text-align: center;
}

@font-face {
  font-family: RobotoThin;
  src: url("../assets/fonts/Roboto-Thin.ttf");
}

@font-face {
  font-family: RobotoMedium;
  src: url("../assets/fonts/Roboto-Medium.ttf");
}

@font-face {
  font-family: RobotoRegular;
  src: url("../assets/fonts/Roboto-Regular.ttf");
}

.App-header {
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: xx-large;
  justify-content: top;
}

.Page-title {
  color: white;
  font-family: RobotoMedium;
  font-size: 60px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.Button {
  background: none;
  text-align: center;
  padding: 15px 32px;
  color: white;
  border-color: white;
  border-width: 1px;
  border-radius: 36px;
  font-family: 'Franklin Gothic Medium';
}

.Background-image {
  background-image: url("../assets/heotw.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-color: rgb(15, 88, 113);
  background-blend-mode: multiply;
  opacity: 75%;
}

.Github-image{
  src: url("../assets/github.png");
}

.Itchio-image {
  src: url("../assets/itchio.png");
}